button {
  cursor: pointer;
  border: none;
  border-radius: 4px;
  margin-right: 10px;
  padding: 20px 40px;
  font-size: 18px;
}

button:last-child {
  margin-right: 0;
}

button:hover {
  box-shadow: 0 12px 16px #0000003d, 0 17px 50px #00000030;
}

a {
  color: #333;
  max-width: 150px;
  background-color: #0000;
  border-radius: 320px;
  margin-bottom: 20px;
  padding: 8px 12px;
  font-size: 16px;
  font-weight: bold;
  text-decoration: none;
}

a:hover {
  box-shadow: -2px -2px 5px #fff, 2px 2px 5px #babecc;
}

